import React from 'react';

function InfoBlock({ title, content }) {
  return (
    <div className="flex flex-col mb-2">
      <span className="text-gray-500 font-medium">{title}</span>
      <span className="text-gray-800">{content}</span>
    </div>
  );
}

function Test({ clientInfo, adminInfo }) {
  return (
    <div className="flex flex-row w-full gap-6">
      <div className="w-1/2 bg-white p-6 rounded-lg shadow-lg">
        <h2 className="text-xl font-medium mb-4">Client Information</h2>
        <InfoBlock title="Name" content={clientInfo.name} />
        <InfoBlock title="Address" content={clientInfo.address} />
        <InfoBlock title="Phone" content={clientInfo.phone} />
        <InfoBlock title="Email" content={clientInfo.email} />
      </div>
      <div className="w-1/2 bg-white p-6 rounded-lg shadow-lg">
        <h2 className="text-xl font-medium mb-4">Admin Information</h2>
        <InfoBlock title="Name" content={adminInfo.name} />
        <InfoBlock title="Address" content={adminInfo.address} />
        <InfoBlock title="Phone" content={adminInfo.phone} />
        <InfoBlock title="Email" content={adminInfo.email} />
      </div>
    </div>
  );
}

export default Test;
