
import { Toaster } from 'react-hot-toast';
import { RouterProvider } from 'react-router-dom';
import './App.css';
import { router } from './Pages/Routes/Router';

function App() {
  return (
    <div className="">
     
    <RouterProvider router={router}></RouterProvider>
    <Toaster></Toaster>
  
    </div>
  );
}

export default App;
